.cl {
    &--darkgray {
        color: $darkGray;
        fill: $darkGray;
        @include heading {
            color: $darkGray; } }
    &--gray {
        color: $gray;
        fill: $gray;
        @include heading {
            color: $gray; } }
    &--gains {
        color: $gainsboro;
        fill: $gainsboro;
        @include heading {
            color: $gainsboro; } }
    &--black {
        color: $black;
        fill: $black;
        @include heading {
            color: $black; } }
    &--green {
        color: #40B2A7;
        fill: #40B2A7;
        @include heading {
            color: #40B2A7; } }
    &--white {
        color: $white;
        fill: $white;
        @include heading {
            color: $white;
            fill: $white; } }
    &--silver {
        color: $silver;
        @include heading {
            color: $silver; } }
    &--blue {
        color: $blueBox;
        @include heading {
            color: $blueBox; } }
    &--red {
        color: $redBox;
        @include heading {
            color: $redBox; } }
    &--lightBlue {
        color: $blueLightBox;
        @include heading {
            color: $blueLightBox; } }
    &--violeta {
        color: #FF5254;
        @include heading {
            color: #FF5254; } }
    &--mora {
        color: #4F40AF;
        fill: #4F40AF;
        @include heading {
            color: #4F40AF; } } }
